#_1HKcN {
  display: flex;
  position: fixed;
  align-items: center;
  width: 100%;
  padding: 10px 20px;
  background-color: var(--bg-color);
  z-index: 9;
  justify-content: space-between;
}

#_1HKcN > span > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

#_1HKcN > span:nth-child(1) {
  font-size: 2em;
  font-weight: bolder;
}

#_1HKcN > span:nth-child(n+3) {
 display: none;
 
}

#_1HKcN > span:nth-child(3) {
  margin-left: 30px;
}

#_1HKcN > span:nth-child(6) {
  flex: 1;
  text-align: right;
}

#_1HKcN > span:not(:first-child):not(:last-child)::after {
  content: '';
  background: black;
  transform-origin: left;
  transform: scale(0);
  transition: transform 500ms ease-in-out;
  height: 1px;
  width: 25px;
  display: block;
  position: absolute;
}

.afHNY {
  display: inline-block;
  cursor: pointer;
}

._z9Br, .u7y_T, .JqnNI {
  width: 35px;
  height: 5px;
  background-color: black;
  margin: 6px 0;
  transition: 0.4s;
}

.DKdOv ._z9Br {
  transform: rotate(-45deg) translate(-9px, 6px);
}

.DKdOv .u7y_T { opacity: 0; }

.DKdOv .JqnNI {
  transform: rotate(45deg) translate(-8px, -8px);
}

#_1HKcN ._3WutE {
  left: auto;
}

@media (min-width: 768px) { 
  #_1HKcN {
    width: calc(100vw - 15px);
    transform: rotate(0deg);
    top: 0%;
    position: absolute;
  }

  #_1HKcN > span:nth-child(5) > i {
    transform: rotate(0deg);
    cursor: pointer;
    margin-left: 25px;
  }

  #_1HKcN > span:nth-child(2) {
    display: none;
  }

  #_1HKcN > span:nth-child(n+3) {   
    margin-left: 10px;
    display: block;
    position: relative;
    text-transform: uppercase;
    font-size: 1em;
  }

  #_1HKcN > span:nth-child(n+6) {
    cursor: pointer;
  }

   #_1HKcN > span:not(:first-child):not(:last-child):hover::after{
    transform: scale(1);
  }
}